import Modal from '@/components/x-modal/x-modal'
export default{
	components:{
		Modal
	},
	data(){
		return{
			Modalshow: false,//弹窗打开隐藏
			windowHeight:0,//屏幕高度
			shade:true,//遮罩
			enterType:-1,//商家入驻类型
			shopName:'',//商户名称
			shoptype:'',//主营分类
			adminName:'',//管理人姓名
			phone:'',//手机号码
			code:'',//手机验证码
			readtype:true,//是否阅读协议
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	onBackPress(){
		if(!this.shade){
			if(!this.Modalshow){
				this.Modalshow = true
				return true
			}
		}
		
	},
	methods:{
		//选择商家入驻类型
		enterTypeClick(num){
			if(num == 0){
				this.enterType = 0
				uni.setNavigationBarTitle({
				    title: '个人入驻'
				});
			}else if(num == 1){
				this.enterType = 1
				uni.setNavigationBarTitle({
				    title: '企业入驻'
				});
			}
			this.shade = false
		},
		//确认退出
		delAddressClcik(){
			uni.navigateBack({})
		},
		//阅读协议
		readClick(){
			this.readtype = !this.readtype
		},
	}
}
